:root {
    --primary-text-color: #ffffff;
    --title-text-color: #D41443;
    --secondary-title-color: #ffa5ba;
    --default-text-color: #000000;
    --secondary-text-color: #907e82;
    --accent-color: #d41443;
    --background-lightpink: #FFF5F6;
    --background-white: #ffffff;
--background-header-pink: #FFB8CA;
    --background-mobile: #E17992;
    --hover-color: #C7133E;
}
%animation {
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
